import React from "react";
import {
    MDBContainer, MDBRow, MDBCol, MDBBtn, MDBIcon, MDBInput, MDBCard, MDBCardBody, MDBCardTitle, MDBCardText, MDBCardHeader
} from 'mdbreact';
import "./Contact.scss";
import { Slide } from "react-awesome-reveal";


const Contact = () => {
    return (
        <MDBContainer fluid id={"contact"} className="contact contact-bg" >
            <Slide direction={"up"} duration={3000} delay={1000} >
                <MDBRow center >
                    <MDBCol lg="8">
                        <form >
                            <p className="h5 text-center mb-4">Írjon nekünk</p>
                            <div className="grey-text">
                                <MDBInput label="Az Ön neve" icon="user" group type="text" validate error="wrong"
                                    success="right" />
                                <MDBInput label="Az Ön email címe" icon="envelope" group type="email" validate error="wrong"
                                    success="right" />
                                <MDBInput label="Tárgy" icon="tag" group type="text" validate error="wrong" success="right" />
                                <MDBInput type="textarea" rows="2" label="Az Ön üzenete" icon="pencil-alt" />
                            </div>
                            <div className="text-center">
                                <MDBBtn outline color="secondary">
                                    Küldés
            <MDBIcon far icon="paper-plane" className="ml-1" />
                                </MDBBtn>
                            </div>
                        </form>
                    </MDBCol>

                </MDBRow>
            </Slide>
            <Slide direction={"down"} duration={3000} delay={1000} >
                <MDBRow center className="contact">
                    <MDBCol lg="6" >
                        <MDBCard  className="contact-card" >
                            <MDBCardHeader>Elérhetőségeink</MDBCardHeader>
                            <MDBCardBody className="text-info">
                                <MDBCardTitle tag="h5">Cégnév:</MDBCardTitle>
                                <MDBCardText>
                                    Elszámolás 2000 Bt.
                                     </MDBCardText>
                                <MDBCardTitle tag="h5">Adószám:</MDBCardTitle>
                                <MDBCardText>
                                    20955067-1-10
                                     </MDBCardText>
                                <MDBCardTitle tag="h5">E-mail:</MDBCardTitle>
                                <MDBCardText>
                                    elszamolas2000bt@gmail.com
                                     </MDBCardText>
                                <MDBCardTitle tag="h5">Telefonszám</MDBCardTitle>
                                <MDBCardText>
                                    +36/30-8696-420
                                    </MDBCardText>
                            </MDBCardBody>
                        </MDBCard>
                    </MDBCol>
                </MDBRow>
            </Slide>


        </MDBContainer>

    );
}

export default Contact;