import React, { Component } from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCollapse, MDBNavItem, MDBContainer, MDBNavLink, MDBIcon } from 'mdbreact';
import Logo from "../../assets/logo.jpg";
import "./Header.scss";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


import withWindowSize from '../hoc/withWindowSize/withWindowSize';
import NavLink from '../nav/NavLink';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: false,
      isWideEnough: false,
    };
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse,
    });
  }
  // className={this.props.page === 0 ? "header-none" : "header-scroll"}

  render() {
    return (
      <MDBContainer fluid>
        <header>
          <MDBNavbar color="stylish-color" fixed="top" dark expand="sm" scrolling transparent>
            <MDBNavbarBrand >
              <img src={Logo} alt="logo" width="50px" /><strong style={{ marginLeft: "10px" }}>Elszámolás 2000 Bt.</strong>
            </MDBNavbarBrand>
            {this.props.isMobileSized && <MDBNavbarToggler onClick={this.onClick} />}
            <MDBCollapse isOpen={this.state.collapse} navbar>
              <MDBNavbarNav right >
                <MDBNavItem>
                  <div className="header-item"><MDBIcon icon="home" /><NavLink title="Kezdőlap" to="welcome" className="header-item--link" /> </div>
                </MDBNavItem>
                <MDBNavItem>
                  <div className="header-item"><MDBIcon icon="info-circle" /><NavLink title="Bemutatkozás" to="home" className="header-item--link" /></div>
                </MDBNavItem>
                <MDBNavItem>
                  <div className="header-item"><MDBIcon icon="chart-pie" /><NavLink title="Szolgáltatásaink" to="service" className="header-item--link" /> </div>
                </MDBNavItem>
                <MDBNavItem>
                  <div className="header-item"><MDBIcon icon="headphones-alt" /><NavLink title="Kapcsolat" to="contact" className="header-item--link" /> </div>
                </MDBNavItem>
              </MDBNavbarNav>
            </MDBCollapse>
          </MDBNavbar>
        </header>
      </MDBContainer>
    );
  }
}

export default withWindowSize(Header);