import { MDBIcon } from "mdbreact";
import React from "react";
import { Pager } from "react-bootstrap";

import ReactPageScroller from 'react-page-scroller';
import Calculator from "../../calculator/Calculator";
import Contact from "../../contact/Contact";
import Footer from "../../footer/Footer";
import Header from "../../header/Header";
import Home from "../../home/Home";
import Service from "../../service/Service";
import Welcome from "../../welcome/Welcome";

import "./Scroll.scss";


export default class Scroll extends React.Component {
  constructor(props) {
    super(props);
    this.state = { currentPage: null };
  }

  handlePageChange = number => {
    this.setState({ currentPage: number }); // set currentPage number, to reset it from the previous selected.
  };

  getPagesNumbers = () => {
    const pageNumbers = [];
    const icons = [<MDBIcon icon="home" size="2x"/>, <MDBIcon icon="info-circle" size="2x" />,
    <MDBIcon icon="chart-pie" size="2x" />, <MDBIcon icon="headphones-alt" size="2x" />]
    const names = ["Kezdőlap", "Bemutatkozás", "Szolgáltatásaink", "Kapcsolat"]

    for (let i = 1; i <= 4; i++) {
      pageNumbers.push(

        <Pager.Item className="pagination-item" key={i} eventKey={i - 1} onSelect={this.handlePageChange}>
          {icons[i - 1]} {names[i - 1]}
        </Pager.Item>

      );
    }

    return [...pageNumbers];
  };

  render() {
    const pagesNumbers = this.getPagesNumbers();
    const footer=this.state.currentPage===3 ? <Footer/>:null;
  
    return (
      <React.Fragment>
        <ReactPageScroller
          pageOnChange={this.handlePageChange}
          customPageNumber={this.state.currentPage}
        >
          <Welcome />
          <Home />
          <Service isThere={this.state.currentPage===2}/>
          <Contact />
        </ReactPageScroller>
        <Header nav={pagesNumbers} page={this.state.currentPage}/>
        {footer}
      </React.Fragment>
    );
  }
}