import { MDBContainer } from "mdbreact";
import React from "react";
import FlipCard from "../flipcard/FlipCard";
import "./Home.scss";
import { Roll,Zoom } from "react-awesome-reveal";


const Home = () => {
    return (
        <MDBContainer fluid className="home" id="home">
            <div className="home-welcome">

                <Roll direction={"right"}  cascade={true} duration={70}
                >
                    Több, mint 20 éve cégünk kezdő és már működő cégek (Kft, Bt),
                     egyéni vállalkozások és non-profit szervezetek teljes körű könyvelését vállalja.</Roll>
                <Zoom direction={"left"}  duration={3000} delay={1000} >
                    <FlipCard className="first" icon="briefcase" text="A könyvelési anyagért szükség szerint Egerben és környékén házhoz megyünk." />
                </Zoom>
                <Zoom direction={"right"} duration={3000} delay={1000}>
                    <FlipCard className="second" icon="at" text=" Gyorsan válaszolunk elektronikus üzeneteire, hívását személyesen fogadjuk." />
                </Zoom>
                <Zoom direction={"down"}  duration={3000} delay={1000}>
                    <FlipCard className="third" icon="university" text="Felsőfokú szakmai képesítéssel rendelkezünk (közgazdász, valamint mérlegképes könyvelő)." />
                </Zoom>
                <Zoom direction={"up"}  duration={3000} delay={1000}>
                    <FlipCard className="fourth" icon="credit-card" text="Ügyfeleinkkel szerződést kötünk, munkánkról számlát adunk, anyagi felelősséget vállalunk." />
                </Zoom>

            </div>
        </MDBContainer>
    )

}

export default Home;
