import React from 'react';
import { MDBCard, MDBCardBody, MDBCardHeader, MDBCardText, MDBCardTitle, MDBIcon, MDBView, } from 'mdbreact';
import "./Card.scss";

const Card = (props) => {
    return (
        <MDBCard className="text-center card-transparent">
            <MDBCardHeader className="card-transparent"  >    <MDBView>
                <MDBIcon icon={props.icon} size="3x" className="logo-color " />
            </MDBView></MDBCardHeader>
            <MDBCardBody className="card-black">
                <MDBCardTitle>{props.title}</MDBCardTitle>
                <MDBCardText className="card-text--size">
                    {props.text}
                </MDBCardText>
            </MDBCardBody>
        </MDBCard>
    )
}

export default Card;