import React from "react";
import { MDBContainer, MDBFooter } from "mdbreact";

import "./Footer.scss";

const Footer = () => {
  return (
    <MDBFooter  color="stylish-color" className="font-small pt-4 mt-4">
      <MDBContainer fluid className="text-center text-md-left">
      <div className="footer-copyright text-center py-3 footer-br">
        <MDBContainer fluid className="footer-fs">
          &copy; {new Date().getFullYear()} Copyright  Elszámolás 2000 Bt. 
        </MDBContainer>
      </div>
      </MDBContainer>
      {/* <div >
        <MDBContainer fluid>
          <a href="https://github.com/Pityubak" >Created by Pityubak</a>   
        </MDBContainer>
      </div> */}
    </MDBFooter>
  );
}

export default Footer;